<template>
    <div class="container">
        <h1 class="text-center my-4">Bienvenido, haz tu consulta</h1>
        <div class="row">
            <div class="col-lg-6">
                <div class="card mb-4">
                    <div class="card-header">
                        <h5 class="card-title">Realiza tu consulta</h5>
                    </div>
                    <div class="card-body">
                        <div class="form-group">
                            <label for="expediente">Expediente:</label>
                            <input type="text" v-model="expediente" class="form-control"
                                placeholder="Ingrese el número de expediente">
                        </div>
                        <button @click="consultarExpediente" class="btn btn-primary">Consultar</button>
                    </div>
                </div>
                <div v-if="error" class="alert alert-danger">{{ error }}</div>
            </div>
            <div class="col-lg-6" v-if="resultado">
                <div class="card mb-4">
                    <div class="card-header">
                        <h5 class="card-title">Detalles del Expediente</h5>
                    </div>
                    <div class="card-body">
                        <div class="form-group">
                            <label>Número de Expediente:</label>
                            <input type="text" class="form-control" :value="resultado.exp_numero" disabled>
                        </div>
                        <div class="form-group">
                            <label>Juzgado:</label>
                            <input type="text" class="form-control" :value="resultado.exp_juzgado" disabled>
                        </div>
                        <div class="form-group">
                            <label>Distrito Judicial:</label>
                            <input type="text" class="form-control" :value="resultado.exp_distrito_judicial" disabled>
                        </div>
                        <div class="form-group">
                            <label>Fecha de Inicio:</label>
                            <input type="text" class="form-control" :value="resultado.exp_fecha_inicio" disabled>
                        </div>
                        <div class="form-group">
                            <label>Estado:</label>
                            <input type="text" class="form-control" :value="resultado.exp_estado" disabled>
                        </div>
                        <!-- Agrega más detalles según la estructura de datos recibida del backend -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from '@/plugins/axios';

export default {
    name: 'Consultas',
    data() {
        return {
            expediente: '',
            resultado: null,
            error: ''
        };
    },
    methods: {
        async consultarExpediente() {
            try {
                const response = await axios.get(`/public/proceeding/${this.expediente}`);
                if (response.status === 200) {
                    this.resultado = response.data.expediente;
                    this.error = ''; // Limpiar el mensaje de error en caso de éxito
                } else {
                    this.error = 'No se pudo obtener la información del expediente. Por favor, inténtelo de nuevo más tarde.';
                    this.resultado = null; // Limpiar el resultado en caso de error
                }
            } catch (error) {
                console.error('Error al consultar el expediente:', error);
                this.error = 'No se pudo obtener la información del expediente. Por favor, inténtelo de nuevo más tarde.';
                this.resultado = null; // Limpiar el resultado en caso de error
            }
        }
    }
};
</script>

<style scoped>
h1 {
    font-size: 2.5rem;
    color: #343a40;
}

.card-header .card-title{
    color: white;
}

.card {
    border-radius: 0.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card-header {
    background-color: #007bff;
    color: white;
    border-bottom: none;
}

.card-title {
    margin: 0;
}

.btn-primary {
    background-color: #007bff;
    border: none;
}

.btn-primary:hover {
    background-color: #0056b3;
}

.alert {
    margin-top: 1rem;
}

.form-control {
    border-radius: 0.25rem;
}
</style>
